import React from "react"
import { Container, Button, Col, Media, Image, Row } from "react-bootstrap"
import "./styles/how-it-works.styles.scss"
import { StaticImage } from "gatsby-plugin-image"
import Footer from "../components/Layout/Footer"
import NavBarLight from "../components/Layout/navBar/NavBarLight"

const HowItWorks = () => (
  <>
    <NavBarLight />
    <Container fluid className="home-container how-it-works p-0  ">
      <Container fluid className=" py-xl-0">
        <Container fluid="xl" className="py-xl-6 d-flex flex-column ">
          <h1 className="h1-header"> How it works </h1>{" "}
          <Row>
            <Col md={6}>
              <p>
                Pave Credit makes borrowing easy and fast when you need it
                most.No more applying for multiple loans. If you’ re approved
                for a Pave Credit line of credit, you can draw money from the
                available credit shown in your Pave account and transfer it to
                your bank account. Here’ s how it works.{" "}
              </p>{" "}
              <h3 className="h3-sub-header"> Apply </h3>{" "}
              <p>
                {" "}
                To qualify for a Pave Credit line of credit you will need:{" "}
              </p>{" "}
              <ol className="main-ol">
                <li>
                  <p>
                    A verifiable source of income that is direct deposited into
                    your checking account{" "}
                  </p>{" "}
                </li>{" "}
                <li>
                  <p>
                    An open checking account that meets our minimum - opened
                    duration requirement{" "}
                  </p>{" "}
                </li>{" "}
                <li>
                  <p> Proof that you’ re at least 18 years old </p>{" "}
                </li>{" "}
              </ol>{" "}
              <p>
                If approved, your available credit limit will be reflected in
                your Pave Credit customer dashboard. Once you make a draw, you’
                ll receive the funds in your bank account usually within two
                business days.{" "}
              </p>
              <h3 className="h3-sub-header"> Initiate a draw </h3>{" "}
              <p>
                Once you specify how much you need, we’ ll deliver only the
                amount you request straight into your bank account usually
                within two business days.{" "}
              </p>
              <p>
                You can request as many draws as you need up to your remaining
                available credit as long as the account is in good standing.As
                you repay each draw, your Pave Credit account builds back up to
                your approved limit.{" "}
              </p>
              <h3 className="h3-sub-header"> Paying back your draws </h3>
              <p>
                At Pave Credit, we make paying back your draws quick and
                simple.Once you’ ve received your draw, you’ ll have a minimum
                payment due at the end of each billing cycle as shown within the
                Pave Credit Statement you will receive. You’ ll only pay
                interest on the outstanding balance.{" "}
              </p>
              <p>
                We align your payments to your pay frequency (weekly, bi -
                weekly, or semi - monthly). Keep in mind, the faster you pay off
                what you owe, the quicker your available line of credit
                refreshes.You can always pay more than your minimum payment, and
                you won’ t be penalized for paying your draw back early.{" "}
              </p>{" "}
            </Col>{" "}
          </Row>{" "}
        </Container>{" "}
      </Container>
      <Container fluid className=" py-xl-0 payment-options-main">
        <Container
          fluid="xl"
          className="py-xl-6 d-flex flex-column pt-5 payment-options"
        >
          <Row>
            <Col md={{ order: "first" }} className="text-center">
              <h1 className="h1-header"> Payment options </h1>{" "}
              <h3 className="h3-sub-header">
                You make pay back your transfers using the following methods:
              </h3>{" "}
            </Col>{" "}
          </Row>{" "}
          <Row className="mt-5">
            <Col>
              {" "}
              <Media className="mb-5">
                <StaticImage
                  className="media-img"
                  src="../images/icon_payment_card.png"
                  alt="Jump Mobile App"
                  placeholder="blurred"
                  width={150}
                  height={150}
                  fit="cover"
                />
                <Media.Body className="align-self-center media-text">
                  <h3 className="mb-1"> Debit card payments </h3>{" "}
                  <p>
                    You can use our online customer portal to make a payment
                    with your debit card.{" "}
                  </p>{" "}
                </Media.Body>{" "}
              </Media>{" "}
            </Col>{" "}
            <Col>
              <Media className="mb-5">
                <StaticImage
                  className="media-img"
                  src="../images/icon_payment_paypal.png"
                  alt="Jump Mobile App"
                  placeholder="blurred"
                  width={150}
                  height={150}
                  fit="cover"
                />
                <Media.Body className="align-self-center media-text">
                  <h3 className="mb-1"> PayPal payments </h3>{" "}
                  <p>
                    You can use ouronline customer portal to make a payment with
                    your debit card.{" "}
                  </p>{" "}
                </Media.Body>{" "}
              </Media>{" "}
            </Col>{" "}
          </Row>{" "}
          <Row>
            <Col className="px-5 py-3 pb-5">
              <p className="px-5 text-center pb-5">
                If at any time you’re unable to make your minimum payment, call
                us immediately at <strong>(855) 801-PAVE</strong> so, together,
                we can work out a solution. We’ll be happy to work with you to
                accommodate your needs.
              </p>{" "}
            </Col>{" "}
          </Row>{" "}
        </Container>{" "}
      </Container>{" "}
    </Container>
    <Footer />
  </>
)

export default HowItWorks
